* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --main-font: "Nunito", sans-serif;
}

html {
  font-size: 14px;
  font-family: var(--main-font);
}

.hide {
  display: none;
}

#errorContainer, .warning {
  color: #db2c11;
  text-align: right;
  font-weight: 700;
}

header {
  text-align: center;
  background: radial-gradient(circle, #e0f3f7cf 0%, #cef6ffdf 100%);
  padding: 1%;
  box-shadow: 0 2px 8px #63636333;
}

h1, h2 {
  color: #033649;
  text-align: center;
  margin-bottom: 10px;
}

h1 {
  margin-top: 10px;
}

p {
  margin-bottom: 10px;
}

a {
  color: #098e71;
  text-decoration: none;
}

a:hover {
  color: #4097ab;
}

#categoryMenu {
  white-space: nowrap;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  font-size: 1em;
  list-style-type: none;
  display: flex;
}

#categoryMenu li {
  color: #098e71;
  margin-right: 5px;
  text-decoration: none;
}

#categoryMenu li a {
  color: #2c7174;
}

#categoryMenu li a:hover {
  color: #4097ab;
  text-decoration: underline;
}

#categoryBoxes {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1%;
  display: flex;
}

.categoryBox {
  max-width: 125px;
  padding: 7px;
}

.categoryBox ul {
  display: block;
}

.categoryBox ul li a {
  color: #098e71;
  font-weight: 600;
  text-decoration: none;
}

.categoryBoxli a:hover {
  color: #066a55;
  text-decoration: underline;
}

.categoryBox h4 {
  text-align: left;
}

img {
  max-width: 100%;
}

#productWrapper {
  width: 97%;
  margin: 15px auto;
}

.searchAndFilter {
  justify-content: space-between;
  align-items: center;
  gap: 20%;
  margin: 0 auto 2%;
  padding: 10px;
  display: flex;
}

form select, form button, form input {
  font-family: var(--main-font);
  padding: 4px;
}

form button {
  padding-left: 15px;
  padding-right: 15px;
}

form input {
  width: 200px;
  max-width: 90%;
}

#filter {
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
  display: flex;
}

#filter select {
  gap: 10px;
  margin: 0 10px 0 0;
}

#productContainer {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.productCard {
  background-color: #e0f3f7;
  flex-direction: column;
  justify-content: space-between;
  min-width: 200px;
  max-width: 300px;
  padding: 20px;
  display: flex;
  box-shadow: 0 1px 3px #00000005, 0 0 0 1px #1b1f2326;
}

.productPrice {
  color: #2bbe1d;
  text-align: center;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  margin: 25px 0 15px;
  font-size: 1.2em;
  font-weight: 600;
}

.productCard div:last-child {
  flex-direction: column;
  align-self: flex-end;
  display: flex;
}

.productCard div:last-child p {
  text-align: right;
}

.brand {
  font-weight: 700;
}

.categoryHeader {
  text-align: center;
  text-transform: uppercase;
  color: #2c7174;
  cursor: pointer;
  margin-bottom: 20px;
  font-weight: 700;
}

.categoryHeader:hover {
  color: #4097ab;
  text-decoration: underline;
}

.categoryHeader:before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: auto;
  font-variant: normal;
  margin: 0 7px 0 5px;
  font-family: "Font Awesome 6 Free";
  font-size: 1.2em;
  font-weight: 900;
  line-height: 1;
  display: inline-block;
}

.categoryTech .categoryHeader:before {
  content: "";
  content: "";
}

.categoryMobile .categoryHeader:before {
  content: "";
}

.categoryLaptops .categoryHeader:before {
  content: "";
}

.categorySkincare .categoryHeader:before {
  content: "";
}

.categoryFragrances .categoryHeader:before {
  content: "";
}

.categoryHome .categoryHeader:before {
  content: "";
}

.categoryGroceries .categoryHeader:before {
  content: "";
}

.categoryClothing .categoryHeader:before {
  content: "";
}

.categoryAccessories .categoryHeader:before {
  content: "";
}

.categoryVehicles .categoryHeader:before {
  content: "";
}

.buyButton {
  color: #fff;
  background-color: green;
  border-radius: 10px;
  align-self: flex-end;
  padding: 10px;
}
